// eslint-disable-next-line import/namespace, import/default, import/no-named-as-default, import/no-named-as-default-member
import axios from 'axios'

import { WEATHER_API_KEY, WEATHER_URL } from '@constants/config'
import {
  WeatherData,
  WeatherForecast,
  WeatherForecastData,
  WeatherForecastItem,
  WeatherHourlyData,
  WeatherParams,
} from '@interfaces/weather'

const weatherCache = new Map<string, WeatherData>()

const fetchHourlyWeather = async (
  params: WeatherParams
): Promise<WeatherHourlyData[]> => {
  const response = await axios.get(`${WEATHER_URL}/forecast`, {
    params: { ...params, appid: WEATHER_API_KEY, units: 'metric' },
  })

  const hourlyForecasts: WeatherHourlyData[] = response.data.list
    .filter((item: WeatherForecastItem) => {
      const date = new Date(item.dt * 1000)
      return date.getUTCHours() % 3 === 0
    })
    .map((item: WeatherForecastItem) => ({
      time: item.dt_txt.split(' ')[1],
      temperature: item.main.temp,
      description: item.weather[0].description,
    }))

  return hourlyForecasts
}

const fetchCurrentWeather = async (
  params: WeatherParams
): Promise<WeatherData> => {
  const cacheKey = params.q
    ? params.q.toLowerCase()
    : `${params.lat},${params.lon}`

  if (weatherCache.has(cacheKey)) {
    return weatherCache.get(cacheKey)!
  }

  const response = await axios.get(`${WEATHER_URL}/weather`, {
    params: { ...params, appid: WEATHER_API_KEY, units: 'metric' },
  })

  const data: WeatherData = {
    temperature: response.data.main.temp,
    description: response.data.weather[0].description,
    cityName: response.data.name,
  }

  weatherCache.set(cacheKey, data)

  return data
}

const fetchWeatherForecast = async (
  params: WeatherParams
): Promise<WeatherForecastData> => {
  const response = await axios.get(`${WEATHER_URL}/forecast`, {
    params: { ...params, appid: WEATHER_API_KEY, units: 'metric' },
  })

  const dailyForecasts: { [key: string]: WeatherForecast } = {}

  response.data.list.forEach((item: WeatherForecastItem) => {
    const date = item.dt_txt.split(' ')[0]
    if (!dailyForecasts[date]) {
      dailyForecasts[date] = {
        date,
        temperature: item.main.temp,
        description: item.weather[0].description,
      }
    }
  })

  const forecastData: WeatherForecastData = Object.values(dailyForecasts)

  return forecastData
}

export const fetchHourlyWeatherByCoordinates = async (
  latitude: number,
  longitude: number
): Promise<WeatherHourlyData[]> => {
  return fetchHourlyWeather({ lat: latitude, lon: longitude })
}

export const fetchHourlyWeatherByCity = async (
  cityName: string
): Promise<WeatherHourlyData[]> => {
  return fetchHourlyWeather({ q: cityName })
}

export const fetchCurrentWeatherByCoordinates = async (
  latitude: number,
  longitude: number
): Promise<WeatherData> => {
  return fetchCurrentWeather({ lat: latitude, lon: longitude })
}

export const fetchCurrentWeatherByCity = async (
  cityName: string
): Promise<WeatherData> => {
  return fetchCurrentWeather({ q: cityName })
}

export const fetchWeatherForecastByCoordinates = async (
  latitude: number,
  longitude: number
): Promise<WeatherForecastData> => {
  return fetchWeatherForecast({ lat: latitude, lon: longitude })
}

export const fetchWeatherForecastByCity = async (
  cityName: string
): Promise<WeatherForecastData> => {
  return fetchWeatherForecast({ q: cityName })
}
