import { FC } from 'react'

import { WeatherCardProps } from '@interfaces/componentProps'

import { Card, Day, Temperature, WeatherIcon } from './StyledCard'

const WeatherCard: FC<WeatherCardProps> = ({ day, image, temperature }) => {
  return (
    <Card>
      <Day>{day}</Day>
      <WeatherIcon src={image} alt={`Weather icon for ${day}`} />
      <Temperature>{temperature}C</Temperature>
    </Card>
  )
}

export default WeatherCard
