import { useState, useEffect } from 'react'

import {
  WeatherData,
  WeatherForecastData,
  WeatherHourlyData,
} from '@interfaces/weather'
import {
  fetchCurrentWeatherByCity,
  fetchCurrentWeatherByCoordinates,
  fetchHourlyWeatherByCity,
  fetchHourlyWeatherByCoordinates,
  fetchWeatherForecastByCity,
  fetchWeatherForecastByCoordinates,
} from '@services/weatherService'

const useWeather = (
  isHourly: boolean,
  city: string,
  latitude?: number | null,
  longitude?: number | null
) => {
  const [hourlyWeather, setHourlyWeather] = useState<
    WeatherHourlyData[] | null
  >(null)
  const [currentWeather, setCurrentWeather] = useState<WeatherData | null>(null)
  const [forecastWeather, setForecastWeather] =
    useState<WeatherForecastData | null>(null)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState('')

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true)
      setError('')

      try {
        const currentDataPromise = city
          ? fetchCurrentWeatherByCity(city)
          : fetchCurrentWeatherByCoordinates(latitude!, longitude!)
        const forecastDataPromise = city
          ? fetchWeatherForecastByCity(city)
          : fetchWeatherForecastByCoordinates(latitude!, longitude!)

        const [currentData, forecastData] = await Promise.all([
          currentDataPromise,
          forecastDataPromise,
        ])

        setCurrentWeather(currentData)
        setForecastWeather(forecastData)

        if (isHourly) {
          const hourlyDataPromise = city
            ? fetchHourlyWeatherByCity(city)
            : fetchHourlyWeatherByCoordinates(latitude!, longitude!)

          const hourlyData = await hourlyDataPromise
          setHourlyWeather(hourlyData)
        }
      } catch (error) {
        if (error instanceof Error) setError(error.message)
      } finally {
        setLoading(false)
      }
    }

    if (!city && (!latitude || !longitude)) {
      setError('City or latitude & longitude are required')
      setLoading(false)
    } else {
      fetchData()
    }
  }, [city, latitude, longitude, isHourly])

  return { currentWeather, forecastWeather, hourlyWeather, loading, error }
}

export default useWeather
