// eslint-disable-next-line import/namespace, import/default, import/no-named-as-default, import/no-named-as-default-member
import styled from 'styled-components'

import { media } from '@constants/screenResolution'

export const Container = styled.section`
  margin: 10% 15%;
  border: 1px solid black;

  @media (max-width: 768px) {
    margin: 10% 5%;
  }
`

export const GridContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(4, 0.6fr);
  grid-column-gap: 10%;
  grid-row-gap: 10px;

  @media (max-width: ${media.smallDesktop}) {
    grid-column-gap: 5%;
  }

  @media (max-width: ${media.tablet}) {
    grid-column-gap: 2%;
  }
`

export const TitleContainer = styled.div`
  grid-area: 1 / 1 / 2 / 2;
  padding: 10px;
`

export const SearchContainer = styled.div`
  grid-area: 1 / 2 / 3 / 3;
  display: flex;
  flex-direction: column;
  align-items: center;

  padding: 10px;
`

export const SignContainer = styled.div`
  grid-area: 2 / 1 / 4 / 2;

  padding: 10px;
`

export const WeatherButnsContainer = styled.div`
  grid-area: 3 / 2 / 4 / 3;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;

  padding: 0 10%;
`

export const WeatherSliderContainer = styled.div`
  grid-area: 4 / 1 / 5 / 3;
`
