import { FC, useCallback, useState } from 'react'

import CitySearch from '@components/CitySearch'
import Title from '@components/Title'
import UserCalendar from '@components/UserCalendar'
import WeatherContainer from '@components/WeatherContainer'
import WeatherToggleButtons from '@components/WeatherToggleButtons'
import useGeolocation from '@hooks/useGeolocation'

import {
  Container,
  GridContainer,
  SearchContainer,
  SignContainer,
  TitleContainer,
  WeatherButnsContainer,
  WeatherSliderContainer,
} from '../../StyledApp'

const Main: FC = () => {
  const [isHourly, setIsHourly] = useState<boolean>(false)
  const [selectedCity, setSelectedCity] = useState<string>('')
  const {
    location: { latitude, longitude },
  } = useGeolocation()

  const handleSetSelectedCity = useCallback((city: string) => {
    setSelectedCity(city)
  }, [])

  const handleButtonClick = useCallback(
    (hourly: boolean) => () => {
      setIsHourly(hourly)
    },
    []
  )

  return (
    <Container>
      <GridContainer>
        <TitleContainer>
          <Title />
        </TitleContainer>
        <SearchContainer>
          <CitySearch setSelectedCity={handleSetSelectedCity} />
        </SearchContainer>
        <SignContainer>
          <UserCalendar />
        </SignContainer>
        <WeatherButnsContainer>
          <WeatherToggleButtons setIsHourly={handleButtonClick} />
        </WeatherButnsContainer>
        <WeatherSliderContainer>
          <WeatherContainer
            isHourly={isHourly}
            selectedCity={selectedCity}
            latitude={latitude}
            longitude={longitude}
          />
        </WeatherSliderContainer>
      </GridContainer>
    </Container>
  )
}

export default Main
