// eslint-disable-next-line import/namespace, import/default, import/no-named-as-default, import/no-named-as-default-member
import axios from 'axios'

import {
  ELASTICSEARCH_API_KEY,
  ELASTICSEARCH_ENDPOINT,
} from '@constants/config'

interface City {
  id: string
  name: string
  country: string
}
interface ElasticsearchResponse {
  hits: {
    hits: Array<{ _source: City }>
  }
}

export const searchCities = async (value: string): Promise<City[]> => {
  const response = await axios.post<ElasticsearchResponse>(
    `${ELASTICSEARCH_ENDPOINT}/cities/_search`,
    {
      query: {
        bool: {
          must: [
            {
              wildcard: {
                name: {
                  value: `*${value}*`,
                  boost: 2,
                },
              },
            },
          ],
        },
      },
      from: 0,
      size: 7000,
    },
    {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `ApiKey ${ELASTICSEARCH_API_KEY}`,
      },
    }
  )

  const results: City[] = response.data.hits.hits
    .map((hit) => hit._source)
    .filter((source) => source.id && source.name && source.country)
    .slice(0, 10)

  return results
}
