import { FC } from 'react'

import { TodayWeatherCardProps } from '@interfaces/componentProps'

import {
  Container,
  Day,
  Description,
  Temperature,
  TextContainer,
  WeatherIcon,
} from './StyledTodayWeatherCard'

const TodayWeatherCard: FC<TodayWeatherCardProps> = ({
  city,
  image,
  temperature,
  description,
}) => {
  return (
    <Container>
      <WeatherIcon src={image} alt={`Weather icon for ${city}`} />
      <TextContainer>
        <Day>Current weather in {city}</Day>
        <Temperature>{temperature}C</Temperature>
        <Description>{description}</Description>
      </TextContainer>
    </Container>
  )
}

export default TodayWeatherCard
