import { FC } from 'react'

import useWeather from '@hooks/useWeather'
import { formatDate, formatTime } from '@utils/formatDateTime'

import {
  Container,
  LoadingIndicator,
  SliderContainer,
  WeatherCardStyle,
} from './StyledWeatherContainer'
import TodayWeatherCard from '../TodayWeatherCard'
import WeatherCard from '../WeatherCard'

interface WeatherDisplayProps {
  isHourly: boolean
  selectedCity: string
  latitude: number | null
  longitude: number | null
}

const WeatherContainer: FC<WeatherDisplayProps> = ({
  isHourly,
  selectedCity,
  latitude,
  longitude,
}) => {
  const { currentWeather, forecastWeather, hourlyWeather, loading, error } =
    useWeather(isHourly, selectedCity, latitude, longitude)

  if (loading) {
    return (
      <Container>
        <LoadingIndicator>Loading...</LoadingIndicator>
      </Container>
    )
  }
  if (error) {
    return <p>Weather Error {error}</p>
  }

  return (
    <Container>
      <TodayWeatherCard
        city={currentWeather?.cityName}
        image={'https://cdn-icons-png.flaticon.com/512/10127/10127236.png'}
        temperature={currentWeather?.temperature}
        description={currentWeather?.description}
      />
      <SliderContainer>
        {isHourly
          ? hourlyWeather?.map((hour, index) => (
              <WeatherCardStyle key={index}>
                <WeatherCard
                  day={formatTime(hour.time)}
                  image={
                    'https://cdn-icons-png.flaticon.com/512/10127/10127236.png'
                  }
                  temperature={hour.temperature}
                  description={hour.description}
                />
              </WeatherCardStyle>
            ))
          : forecastWeather?.map((forecast) => (
              <WeatherCardStyle key={forecast.date}>
                <WeatherCard
                  day={formatDate(forecast.date)}
                  image={
                    'https://cdn-icons-png.flaticon.com/512/10127/10127236.png'
                  }
                  temperature={forecast.temperature}
                  description={forecast.description}
                />
              </WeatherCardStyle>
            ))}
      </SliderContainer>
    </Container>
  )
}

export default WeatherContainer
