import { FC, useEffect, useState, memo } from 'react'

import Input from '@components/UI/Input'
import useDebounce from '@hooks/useDebounce'
import { searchCities } from '@services/cityService'

import { CityItem, CityList, Container, Text } from './StyledCitySearch'
import { City, CitySearchProps } from './TypesCitySearch'

const CitySearch: FC<CitySearchProps> = ({ setSelectedCity }) => {
  const [inputValue, setInputValue] = useState<string>('')
  const [cities, setCities] = useState<City[]>([])
  const [error, setError] = useState('')
  const debounceInputValue = useDebounce(inputValue, 500)

  useEffect(() => {
    const fetchCities = async () => {
      if (debounceInputValue) {
        try {
          const results = await searchCities(debounceInputValue)
          setCities(results)
        } catch (error) {
          if (error instanceof Error) setError(error.message)
        }
      } else {
        setCities([])
      }
    }

    fetchCities()
  }, [debounceInputValue])

  const handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInputValue(event.target.value)
  }

  const handleCitySelect = (city: string) => () => {
    setSelectedCity(city)
  }

  return (
    <Container>
      <Input
        type="text"
        value={inputValue}
        onChange={handleChange}
        placeholder="Enter City..."
      />
      <CityList>
        {error ? (
          <Text>{error}</Text>
        ) : (
          cities.map(({ id, name, country }) => (
            <CityItem key={id} onClick={handleCitySelect(name)}>
              <Text>{name}</Text>
              <Text>{country}</Text>
            </CityItem>
          ))
        )}
      </CityList>
    </Container>
  )
}

export default memo(CitySearch)
