import { FC } from 'react'

import { InputProps } from '@interfaces/componentProps'

import { InputContainer, StyledInput } from './StyledInput'

const Input: FC<InputProps> = ({ value, onChange, placeholder, width }) => {
  return (
    <InputContainer width={width}>
      <StyledInput
        type="text"
        value={value}
        onChange={onChange}
        placeholder={placeholder}
      />
    </InputContainer>
  )
}

export default Input
