// eslint-disable-next-line import/namespace
import { createGlobalStyle } from 'styled-components'

export const GlobalStyles = createGlobalStyle`
    *{
        margin: 0;
        padding: 0;
        box-sizing: border-box;
    }
    body{
        height: 100vh;
    }
    a{
        text-decoration: none;
        color: inherit
    }
    button{
        cursor: pointer;
    }
`
