// eslint-disable-next-line import/namespace
import { gapi } from 'gapi-script'
import { useEffect, useState } from 'react'

import { GOOGLE_API_KEY, GOOGLE_CALENDAR_CLIENT_ID } from '@constants/config'
import { CalendarEvent } from '@interfaces/calendar'

const useGoogleCalendar = () => {
  const [events, setEvents] = useState<CalendarEvent[]>([])
  const [isSignedIn, setIsSignedIn] = useState<boolean>(false)
  const [error, setError] = useState<string>('')

  useEffect(() => {
    const initClient = async () => {
      await gapi.client.init({
        apiKey: GOOGLE_API_KEY,
        clientId: GOOGLE_CALENDAR_CLIENT_ID,
        scope: 'https://www.googleapis.com/auth/calendar.readonly',
      })

      await gapi.client.load('calendar', 'v3')

      const auth = gapi.auth2.getAuthInstance()
      const signedIn = auth.isSignedIn.get()
      setIsSignedIn(signedIn)

      if (signedIn) {
        loadEvents()
      }

      auth.isSignedIn.listen((isSignedIn: boolean) => {
        setIsSignedIn(isSignedIn)
      })
    }

    gapi.load('client:auth2', initClient)
  }, [])

  useEffect(() => {
    if (isSignedIn) {
      loadEvents()
    } else {
      setEvents([])
    }
  }, [isSignedIn])

  const loadEvents = async () => {
    try {
      const response = await gapi.client.calendar.events.list({
        calendarId: 'primary',
        timeMin: new Date().toISOString(),
        maxResults: 10,
        singleEvents: true,
        orderBy: 'startTime',
      })

      const fetchedEvents = response.result.items || []
      setEvents(fetchedEvents)
    } catch (error) {
      if (error instanceof Error) setError(error.message)
    }
  }

  const signIn = async () => {
    const authInstance = gapi.auth2.getAuthInstance()
    await authInstance.signIn()
  }

  const signOut = () => {
    gapi.auth2.getAuthInstance().signOut()
  }

  return { events, isSignedIn, error, signIn, signOut }
}

export default useGoogleCalendar
