import { FC } from 'react'

import Button from '@components/UI/Button/Button'
import useGoogleCalendar from '@hooks/useGoogleCalendar'

import {
  // BtnsContainer,
  CalendarContainer,
  Container,
} from './StyledUserCalendar'

const UserCalendar: FC = () => {
  const { events, isSignedIn, signIn, signOut } = useGoogleCalendar()
  return (
    <Container>
      {isSignedIn ? (
        <>
          <Button onClick={signOut} btnText={'Sign Out'} />
          <CalendarContainer>
            {events.map(({ id, summary, start }) => (
              <div key={id}>
                <p>{summary}</p>
                <p>{start.dateTime || start.date}</p>
              </div>
            ))}
          </CalendarContainer>
        </>
      ) : (
        <Button onClick={signIn} btnText={'Sign In'} />
      )}
    </Container>
  )
}

export default UserCalendar
