import { FC } from 'react'

import Button from '@components/UI/Button'

export interface WeatherToggleButtonsProps {
  setIsHourly: (hourly: boolean) => () => void
}

const WeatherToggleButtons: FC<WeatherToggleButtonsProps> = ({
  setIsHourly,
}) => {
  return (
    <>
      <Button onClick={setIsHourly(false)} btnText={'Daily'} />
      <Button onClick={setIsHourly(true)} btnText={'Hourly'} />
    </>
  )
}

export default WeatherToggleButtons
