import { FC, useEffect, useState } from 'react'

import { getLocaleDateTime } from '@utils/getLocaleDateTime'

import { Container, Day, Time } from './StyledTitle'

const Title: FC = () => {
  const [time, setTime] = useState<string>('')
  const [date, setDate] = useState<string>('')

  const updateDateTime = () => {
    const [newTime, newDate] = getLocaleDateTime()
    setTime(newTime)
    setDate(newDate)
  }

  useEffect(() => {
    updateDateTime()
    const intervalID = setInterval(updateDateTime, 1000)
    return () => clearInterval(intervalID)
  }, [])

  return (
    <Container>
      <Time>{time}</Time>
      <Day>{date}</Day>
    </Container>
  )
}

export default Title
